import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { IonicPage, NavController, LoadingController, NavParams, AlertController, ToastController, ModalController, MenuController } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { UserProvider } from '../../providers/user/user';
import { TranslatePipe, TranslateService, LangChangeEvent } from '@ngx-translate/core';
import { NewModalPage } from '../new-modal/new-modal';

@IonicPage({
  name: 'page-auth3',
  segment: 'auth3',
  priority: 'high'
})

@Component({
  selector: 'page-auth3',
  templateUrl: 'auth-3.html'
})
export class AuthPage3 implements OnInit {
  public onLoginForm: FormGroup;
  public onRegisterForm: FormGroup;
  auth: string = "register";
  tags = [];
  arr = [];
  userOrderedArr = [];
  originalArr = [];
  tag: string;
  tags2 = [];
  tagsnew = [];
  disable: boolean = true;
  disable1: boolean = false;
  counter: number = 0;
  backspace: boolean = false;
  SecondScreenTags = [];
  completeprocess: boolean = false;
  endStep: boolean = true;
  data: any;
  tags1 = [];
  tagsoriginal = [];
  show: boolean = false;
  email: any;
  password: any;
  country: any;
  count: any;



  constructor(private _fb: FormBuilder, public loadingCtrl: LoadingController, public translate: TranslateService, 
    public user: UserProvider, public params: NavParams, public nav: NavController, public forgotCtrl: AlertController, 
    public menu: MenuController, public toastCtrl: ToastController, private storage: Storage, public modalCtrl: ModalController) {
    this.translate.setDefaultLang('en');
    this.translate.use('es');
    this.menu.swipeEnable(false);
    this.menu.enable(false);
    this.storage = storage;
    this.email = this.params.get('email');
    this.password = this.params.get('password');
    this.country = this.params.get('country');
    this.count = this.params.get('count');
    this.arr = this.params.get('tags');

    if (this.arr != null) {
      this.arr = this.shuffle(this.arr);
    }

    

  }

  openModal(message: string) {
    var msg:string = "";
    
    this.translate.get(msg).subscribe(async (translated: string) => {
      var content = this.translate.instant(translated);
      const helpModal = this.modalCtrl.create(NewModalPage, { html: content });
      await helpModal.present();
    });
  }

  goBack(){
     //go back to word create page
    this.nav.setRoot('page-auth2', { 'country': this.country, 'email': this.email, 'password': this.password });
  
  }

  showWords(fab:any){
    fab.close();
    this.openModal('Auth-page3.page-show-words');
  }


  //randomise words
  shuffle(arr: any) {

    //create a copy of original array
    arr.forEach(item => {
      this.originalArr.push(item);
    });

    //shuffle array data
    let i:number, j:number, temp:number;
    for (i = arr.length - 1; i > 0; i--) {
      j = Math.floor(Math.random() * (i + 1));
      temp = arr[i];
      arr[i] = arr[j];
      arr[j] = temp;
    }

    return arr;
  };



  // check regualar expression
  orderItem(itemName, i) {
    let pos = this.userOrderedArr.length;

    console.log("UOA Pos = " + pos);
    console.log("itemName = " + itemName);
    console.log("OA = " + this.originalArr[pos]);

    if (itemName.trim() === this.originalArr[pos].trim()) {
      this.userOrderedArr.push(itemName.trim());
      this.arr.splice(i, 1);

      if (this.arr.length === 0) {
        this.disable = false;
        return;
      }

    } else {

      this.translate.get('resgister-pag-3.word_order_error').subscribe((translated: string) => {

        var toast3 = this.translate.instant('resgister-pag-3.word_order_error');
        let toast = this.toastCtrl.create({
          message: toast3,
          duration: 1000,
          position: 'top'
        });

        toast.onDidDismiss(() => { });

        toast.present();

      });

    }

    return;

  }


  deadend() {
    return false;
  }
  // Tags change to call 
  onChange(val) {
   
   return false;
  }

  // verify tags
  verifyTag(str: string): boolean {
    /*
        return str !== 'ABC' && str.trim() !== '';
        */
       return false;
  }


  ngOnInit() {

  }

   //DEBUG CODE : REMOVE LATER
 isJson(str:any) {
    try {
        JSON.parse(str);
    } catch (e) {
        return false;
    }
    return true;
}

  // check tags    
  completeRegister() {

    this.translate.get('Auth-page').subscribe((translated: string) => {

      var loader = this.translate.instant('Auth-page.loader');

      let loading = this.loadingCtrl.create({
        content: loader,
      });

      loading.present();

      //DEBUG CODE : REMOVE LATER
      if(this.isJson(this.country)){
        console.log("JSON looks valid");
      } else {
        console.log("Invalid JSON:" + this.country);
      }

      console.log("Country string: "+this.country);

      let countryJson = null;

      try{
        countryJson = JSON.parse(''+this.country);
      }catch(error){
        console.log(error.message);
        countryJson = {"Name":"South Africa","value":"710"};
      }

      //remove any old user data
      this.user.removeTempUserData();

      this.user.register(this.userOrderedArr, this.email, this.password, countryJson.value).subscribe(
        data => {

          let response = JSON.parse(JSON.stringify(data));
          let body = JSON.parse(response._body);
        
          if (body.success) {

            this.translate.get('resgister-pag-3.success_activate_email_toast').subscribe((translated: string) => {
              var toast5 = this.translate.instant('resgister-pag-3.success_activate_email_toast');

              let toast = this.toastCtrl.create({
                message: toast5,
                duration: 3000,
                position: 'top'
              });

              toast.onDidDismiss(() => {
                console.log('Dismissed toast');
                localStorage.removeItem('check');
                this.nav.setRoot('page-auth');
              });

              toast.present();

            })

          } else {

            let message = (body.message && body.message != null) ? body.message : "Unspecified error.";

            this.translate.get('resgister-pag-3.registration_unsuccessful').subscribe((translated: string) => {
              var toast5 = this.translate.instant('resgister-pag-3.registration_unsuccessful' + message);

              let toast = this.toastCtrl.create({
                message: toast5,
                duration: 3000,
                position: 'top'
              });

              toast.onDidDismiss(() => {
                localStorage.removeItem('check');
                this.nav.setRoot('page-auth');
              });

              toast.present();

            })



          }
        }, error => {
          console.log(error.JSON);
          console.log(error.JSON);
          setTimeout(() => {
            loading.dismiss();
            this.nav.setRoot('page-general_error');
          }, 1000);
        },
        () => {


          loading.dismiss();


        })

    })

  }

}



